import {type} from './type';

const initState={
    check:{},
    data:[],
    dataAll:[{cont:'',id:1},{cont:'',id:2},{cont:'',id:3}],
}
export default function(state=initState,action){
    switch(action.type){
        case type.change:
            return {
                ...state,
                data:action.data
              
            }
        case type.checked:
            return {
                ...state,
                check:action.data
            }
        default:
            return state
    }
}


